<template>
    <div class="character" v-for="char in character" :key="char">
        <div class="character__img">
            <div class="dark-gradient"></div>
            <img class="character__img-item" :src="url" :alt="char.name"/>
        </div>

        <main class="character__info">
            <section>
                <h1 class="title">{{char.name}}</h1>
                <p class="modified">Last time modified: {{this.dateModified || "No data"}}</p>
                <!-- <FavButton /> -->
                <p class="character__info-bio">{{char.description || "This character's bio couldn't be found"}}</p>
            </section>

            <section class="character__info-comics">
                <h3>Comics</h3>
                <ul>
                    <li v-for="comic in comics" :key="comic">
                        <div class="character__info-comics__img">
                            <a :href="comic.urls[0].url">
                                <img :src="comic.images[0].path + this.extension" :alt="comic.title"/>
                            </a>
                        </div>
                        {{comic.title || "No comics for this character"}}
                        </li>
                </ul>
            </section>

            <section class="character__info-series">
                <h3>Series</h3>
                <ul>
                    <li v-for="serie in series" :key="serie">
                        <a :href="serie.urls[0].url">
                            {{serie.title || "No series for this character"}}
                        </a>
                        </li>
                </ul>
            </section>

            <section class="character__info-stories">
                <h3>Stories</h3>
                <ul>
                    <li v-for="story in stories" :key="story">{{story.title || "No stories for this character"}}</li>
                </ul>
            </section>
        </main>
    </div>

    
</template>

<script>
import {api, public_key, hash} from '../marvel'
import axios from 'axios'

export default {
    name: 'Character',

    data() {
        return {
            character: [],
            url: '',
            comicImg: [],
            extension: '.jpg',
            comics: [],
            series: [],
            stories: [],
            dateModified: ''
        }
    },

    mounted() {
        this.getCharacter();
        this.getComics();
        this.getSeries();
        this.getStories();
    },

    methods: {
        getCharacter() {
            let characterId = this.$route.params.id
            axios.get(`${api}/${characterId}?ts=1000&apikey=${public_key}&hash=${hash}`)
            .then((res) => {
              res.data.data.results.forEach((item) => {
                this.character.push(item)

                this.url = `${item.thumbnail.path}${this.extension}`

                this.dateModified = (item.modified).slice(0, 10)
              })
            })
            .catch((error) => {
              console.log(error)
            })
        },
        getComics() {
            let characterId = this.$route.params.id
            axios.get(`${api}/${characterId}/comics?ts=1000&apikey=${public_key}&hash=${hash}&limit=6&offset=0`)
            .then((res) => {
              res.data.data.results.forEach((item) => {
                 
                this.comics.push(item)

              })
            })
            .catch((error) => {
              console.log(error)
            })
        },
        getSeries() {
            let characterId = this.$route.params.id
            axios.get(`${api}/${characterId}/series?ts=1000&apikey=${public_key}&hash=${hash}&limit=6&offset=0`)
            .then((res) => {
              res.data.data.results.forEach((item) => {
                 
                this.series.push(item)
               

              })
            })
            .catch((error) => {
              console.log(error)
            })
        },
        getStories() {
            let characterId = this.$route.params.id
            axios.get(`${api}/${characterId}/stories?ts=1000&apikey=${public_key}&hash=${hash}&limit=6&offset=0`)
            .then((res) => {
              res.data.data.results.forEach((item) => {
                 
                this.stories.push(item)

              })
            })
            .catch((error) => {
              console.log(error)
            })
        }
    },
    
}
</script>
